import ReactPlayer from "react-player"
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from "../../../../utils"

const Video = (props) => {
    const { videoRef, inViewport, item, wall, setVideoLoaded, playing, onPlayingVideo, videoLoaded, handleAllImageError } = props
    return (
        <>
            <ReactPlayer
                ref={videoRef}
                poster={item.postFileNew}
                // style={{ display: 'contents' }}
                className="tb-detail-image-iframe"
                url={inViewport ? item.mediaUrl : ''}
                data-type="video"
                data-network={item.networkId}
                data-link={item.link}
                data-wall-id={wall.Wall.id} data-item-id={item.id}
                data-load={0}
                config={{
                    facebook: {
                      appId: '117029975354072',
                      attributes: {
                        'data-width': 'auto',
                        'data-height': `${document.querySelector('.ts_rt_post_in') ? document.querySelector('.ts_rt_post_in').clientHeight : '100%'}`
                      }
                    }
                  }}
 
                onError={(e) => { if (inViewport) {IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e);setVideoLoaded(false)} }}
                autoPlay={inViewport}
                muted
                onReady={(e) => setVideoLoaded(true)} 
                playing={playing}
                height={'100%'}
                width={'auto'}
                onProgress={onPlayingVideo}
                style={videoLoaded && inViewport ? { position: 'relative' } : { display: 'none' }}
            />

            {videoLoaded ?
                <></>
                :
                <div className={'ts_rt_image'} >
                    <img loading="lazy" data-src={item.postFileNew} src={item.postFileNew} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} width="auto" height="100%" className={`imgID${item.id}`} onError={(e) => { if (item.networkId !== `28`) handleAllImageError(e) }} alt="post" />
                </div>
            }
        </>
    )
}

export default Video