import React, { PureComponent } from "react";
import Card from "./elements/Card";
import { connect } from 'react-redux';
import { numPost, IS_SCREEN_SHOT , findFromArray, removeTransFromCss, IMG_LOAD_AFTER_COMPLETE_LAYOUT } from '../../../utils'
import { getDataNextSteps , themePostTracking, showPopUP } from "../../../../../../../../../actions/themeActions/themeActions";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';

import { Autoplay, Mousewheel } from 'swiper';


class ReelTheme extends PureComponent {
  allImageQueue = [];
  state = {
    allFirstImage: false,
    windowWidth: document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth : ''
  }
  constructor(props) {
    super(props)
    this.secondaryRef = React.createRef();
    this.slideRef = React.createRef();
  }
  componentDidMount(){
    const { wall, hasMoreData, postData } = this.props;
    if(wall.ThemeRule.numberOfCoumn == postData.length && hasMoreData) this.onUpdateData()
  }
  onUpdateData = () => {
    setTimeout(() => this.requestData(), 1000)
  }

  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    console.log('wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent', wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent)
 
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }

  onLoadPopup = (itemIndex, itemData) => {
    const {  wall,   postData, languageSetting, completeDataObject, wallId, webFilters } = this.props;
    let updatePostData = postData.map(itemData => completeDataObject[itemData])
    let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_")) 
    let currentIndex = findFromArray(filteredPostData, itemData.id, itemIndex)

    if (wall.Personalization.postFeatured === 1) {
      removeTransFromCss(`add`);
      this.props.showPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
    } themePostTracking({ action: 2, wall: wallId, feed: itemData.feedId, post: itemData.referenceId })
  }


  handleAllImageError = (e) => {

    try {
      const src = e.target.getAttribute("data-item-id");
      
      // if (!this.allImageQueue.some(i => i.src.includes(src))) {
        this.allImageQueue.push({ src, e });
        if (!this.state.allFirstImage) this.processAllImageQueue();
        // if (!this.state.allFirstImage) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => {});
      // }
    } catch (error) {
      console.error("error",error)
    }

  };

  processAllImageQueue = () => {
    if (this.allImageQueue.length) {
      this.setState({ allFirstImage: true })
      const { src, e } = this.allImageQueue[0];
      if (src) {
        IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, this.nextProcessAllImageQueue)
      }
    }
  };
  nextProcessAllImageQueue = () => {
    try {
      this.allImageQueue.shift();
      this.processAllImageQueue()
    } catch (error) {
    }
  }


  render() {
    const { completeDataObject, hasMoreData, wall, newPostData , loaderData, postData } = this.props;
    const { windowWidth } = this.state;

    const columnCount = wall.Personalization.columnCount;
    const columnSize = wall.ThemeRule.numberOfCoumn;
    const mobileColumnSize = wall.ThemeRule.mobileColumn;
    let themeWidth = columnCount >= 7 ? "100%" : columnCount === 5 && windowWidth > 767 ? "90%" : columnCount === 3 && windowWidth > 767 ? "70%" : "100%"

    const slidePost = wall.ThemeRule.slidePost;
    const sliderSettings1 = {
      type: 'slider',
      perPage: columnSize == 0 ? 6.2 : columnSize  + 0.2,
      autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
      perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 6 : columnSize,
      pagination: false,
      breakpoints: {
        1400: {
          // type: postCount < (columnSize == 0 ? 5 : columnSize) ? false : 'loop',
          perPage: columnSize == 0 ? 5.2 : columnSize+0.2,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 5 : columnSize,
        },
        1024: {
          // type: postCount < (columnSize == 0 ? 4 : columnSize) ? false : 'loop',
          perPage: columnSize == 0 ? 4.2 : columnSize+0.2,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : columnSize == 0 ? 4 : columnSize,
        },
        767: {
          // type: postCount < (columnSize == 0 ? 3 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 3.2 : mobileColumnSize+0.2,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 3 : mobileColumnSize,
        },
        560: {
          // type: postCount < (columnSize == 0 ? 2 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 2 : mobileColumnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 2 : mobileColumnSize,
        },
        480: {
          // type: postCount < (columnSize == 0 ? 1 : columnSize) ? false : 'loop',
          perPage: mobileColumnSize == 0 ? 1 : mobileColumnSize,
          perMove: wall.ThemeRule.slidePost == 1 ? 1 : mobileColumnSize == 0 ? 1 : mobileColumnSize,
        },
      },
      arrows: false,

    }
    


    return (
      <div className="ts_csl_post_container" id="ts_theme_container">

        <Swiper
          // style={{ width: themeWidth }}
          // onScroll={(swiper) => { if (hasMoreData && swiper.progress > 0.8) this.onUpdateData(); }}
          // onAutoplay={(swiper) => {if(hasMoreData && swiper.progress > 0.8 ) this.onUpdateData();}}
          autoHeight={true}
          speed={300}
          forceToAxis={true}
          slidesPerView={columnSize == 0 ? 6.2 : columnSize  + 0.5}
          touchRatio={2}
          // cssMode={isMobile}
          passiveListeners={true}
          height={'250px'}
          freeMode={{enabled:true,sticky: false, momentumBounceRatio: 2, momentumVelocityRatio: 3}}
          autoplay={{ delay: slidePost ? wall.ThemeRule.slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true, }}
          breakpoints={{
            320: {
              slidesPerView: mobileColumnSize == 0 ? 1.6: mobileColumnSize + 0.6
            },
            480: {
              slidesPerView: mobileColumnSize == 0 ? 1.6: mobileColumnSize + 0.6
            },
            575: {
              slidesPerView: mobileColumnSize == 0 ? 1.6: mobileColumnSize + 0.6
            },
            767: {
              slidesPerView: columnSize == 0 ? 3.6 : columnSize+0.6,
            },
            1024: {
              slidesPerView: columnSize == 0 ? 4.6 : columnSize+0.6,
            },
            1400: {
              slidesPerView: columnSize == 0 ? 5.6 : columnSize+0.6,
            }
          }}
          mousewheel={{ forceToAxis: true }}
          watchSlidesProgress={true}
          modules={[Autoplay, Mousewheel]} className="ts_st_mySwiper">
          {(postData && postData.length > 0) ? postData.map((item, index) => {
            const cardData = completeDataObject[item];
            return <SwiperSlide key={`poid_${index}`} ref={this.slideRef} >
               {({ isVisible }) => (
                <Card handleAllImageError={this.handleAllImageError} isVisible={isVisible} slideRef={this.slideRef} itemData={cardData} key={`post_id_${index}`} itemIndex={index} {...this.props} />
              )}
            </SwiperSlide>
          }) : null
          }
        </Swiper>

        {/* <Splide hasTrack={false} options={sliderSettings1} onMove={(splide, index, prev, dest) => {if(hasMoreData && postData.length - 1 <= dest ) this.onUpdateData()}}  ref={this.secondaryRef} onDragging={(splide) => {if(hasMoreData) this.onUpdateData()}} >
          <SplideTrack>
            {
              (postData && postData.length > 0) ? postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide ref={this.slideRef} >
                  <Card handleAllImageError={this.handleAllImageError} slideRef={this.slideRef} itemData={cardData} key={`post_id_${index}`} itemIndex={index} {...this.props} />
                  </SplideSlide>
              }) : null
            }
          </SplideTrack>


        </Splide> */}
      </div>
    );
  }
}


const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent)),
    showPopUP: (data) => dispatch(showPopUP(data)),

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ReelTheme);